.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

:root {
  --pimary-text-color: #2c2c2c;
}

body {
  color: var(--pimary-text-color);
  font-family: Roboto, sans-serif;
}

h1, h2, h3, h4, ul, p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
  display: block;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.favorites__title {
  text-align: center;
  color: var(--pimary-text-color);
  margin-bottom: 20px;
  font-size: 28px;
  line-height: 1.17857;
}

@media screen and (min-width: 768px) {
  .favorites__title {
    font-size: 42px;
    line-height: 1.16;
  }
}

.favorites__text {
  text-align: center;
  color: var(--pimary-text-color);
  margin-bottom: 60px;
  font-size: 14px;
  line-height: 1.14286;
}

@media screen and (min-width: 768px) {
  .favorites__text {
    font-size: 16px;
    line-height: 1.16;
  }
}

.favorites__list {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .favorites__list {
    flex-flow: wrap;
    justify-content: center;
    gap: 32px;
  }
}

.favorites__item {
  justify-content: center;
  align-items: center;
  list-style: none;
  display: inline-flex;
}

.favorites__item:nth-child(n+4) {
  display: none;
}

@media screen and (min-width: 768px) {
  .favorites__item:nth-child(n+4) {
    display: flex;
  }
}

.favorites__wrapper {
  cursor: pointer;
  border: 11px solid #ffe550;
  border-radius: 50%;
  padding: 18px 17px;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .favorites__wrapper {
    border: 14px solid #ffe550;
    padding: 21px 20px;
  }
}

@media screen and (min-width: 1280px) {
  .favorites__wrapper {
    border: 16px solid #ffe550;
    padding: 25px 24px;
  }
}

.favorites__wrapper:hover {
  border-color: #bbf330;
}

